   margin-left: auto;
   margin-right: auto;
}
.pull-right {
   float: right !important;
}
.pull-left {
   float: left !important;
}
*,
*:before,
*:after {
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
}



